<template>
    <layout :showBack="true" title="收货单详情">
        <div class="content-detail">
            <div class="info">
                <span>创建人：{{detail.userName}}</span>
                <span>创建时间：{{detail.createTime | dateFormat('yyyy年MM月dd日')}}</span>
                <span>单据状态：{{detail.state}}</span>
                <span>项目名称：{{detail.projectName}}</span>
                <span>材料名称：{{detail.materialName}}</span>
                <span>采购数量：{{detail.materialWeight}}</span>
                <span>计量单位：{{detail.materialUnit}}</span>
                <span>单价：{{detail.price}}</span>
                <span>供应商名称：{{detail.supplierName}}</span>
                <span>联系人：{{detail.supplierContact}}</span>
                <span>联系电话：{{detail.supplierPhone}}</span>
            </div>
            <van-steps direction="vertical" :active="remark.length - 1">
                <van-step v-for="(it, i) in remark" :key="i">
                    <div class="step-info-title">{{it.userName}}</div>
                    <div class="step-info-text">{{it.time | dateFormat('yyyy年MM月dd日')}}</div>
                    <div class="step-info-text">{{it.remark}}</div>
                </van-step>
            </van-steps>
        </div>
    </layout>
</template>



<script>
    import { receiveInfoApi } from '../../apis/ReceiveApi'

    export default {
        data() {
            return {
                remark: [],
                detail: {
                    userName: '',
                    createTime: '',
                    state: '',
                    projectName: '',
                    materialName: '',
                    materialWeight: '',
                    materialUnit: '',
                    price: '',
                    supplierName: '',
                    supplierPhone: '',
                    supplierContact: ''
                }
            }
        },
        async created() {
            let result = await receiveInfoApi(this.$route.query.id)
            if (result.code === 200) {
                this.remark = result.data.remark || []
                for (let name in this.detail) {
                    this.detail[name] = result.data[name] || ''
                }
            }
        }
    }
</script>