import Ajax from './Ajax'

const receiveAddApi = data => {
    return Ajax({
        url: '/receiving/save',
        method: 'POST',
        data
    })
}

const receiveSaveApi = data => {
    return Ajax({
        url: '/receiving/update',
        method: 'POST',
        data
    })
}

const receiveDeleteApi = id => {
    return Ajax({
        url: '/receiving/delete/' + id,
        method: 'POST'
    })
}

const receiveInfoApi = id => {
    return Ajax({
        url: '/receiving/info',
        params: {id}
    })
}

const receiveListApi = params => {
    return Ajax({
        url: '/receiving/list',
        params
    })
}

const receiveFinishApi = data => {
    return Ajax({
        url: '/receiving/finish',
        method: 'POST',
        data
    })
}

const receiveCollectApi = data => {
    return Ajax({
        url: '/receiving/collection',
        method: 'POST',
        data
    })
}

export {
    receiveAddApi,
    receiveSaveApi,
    receiveDeleteApi,
    receiveInfoApi,
    receiveListApi,
    receiveFinishApi,
    receiveCollectApi
}